import './index.html'
import 'normalize.css'
import './index.scss'
import {gsap, ScrollTrigger, ScrollSmoother} from "gsap-trial/all";

gsap.registerPlugin(ScrollTrigger, ScrollSmoother); 


window.addEventListener('scroll', () => {
    document.body.style.setProperty('--scrollTop', window.pageYOffset + 'px')
})

if (ScrollTrigger.isTouch !== 1) {
    ScrollSmoother.create({
        wrapper: '.wrapper',
        content: '.content',
        smooth: 1.5,
        effects: true,
    })
}

gsap.fromTo('.hero-section', {opacity: 1}, {
    opacity: 0,
    scrollTrigger: {
        trigger: '.hero-section',
        start: 'center',
        end: 'bottom',
        scrub: true
    }
})

const itemsLeft = gsap.utils.toArray('.gallery__left .gallery__item')
itemsLeft.forEach((item) => {
    gsap.fromTo(item, {x: -100, opacity: 0}, {
        opacity: 1,
        x: 0,
        scrollTrigger: {
            trigger: item,
            scrub: true
        }
    })
})

const itemsRight = gsap.utils.toArray('.gallery__right .gallery__item')
itemsRight.forEach((item) => {
    gsap.fromTo(item, {x: 100, opacity: 0}, {
        opacity: 1,
        x: 0,
        scrollTrigger: {
            trigger: item,
            scrub: true
        }
    })
})
